*{
    padding:0;
    margin:0;
}

@import url('https://fonts.googleapis.com/css?family=Roboto:400,700,900&display=swap');

body{
    background-color: #f2f1ef;;
}


/* 
    Navigation Bar 
*/

.navBar{
    padding-bottom:35em;
    clip-path: ellipse(81% 56% at 30% 2%);
    font-family: 'Roboto', sans-serif;
    background-color: #77af74;
    font-size:1.5em; 
    position: relative;
    padding-left:1em
}

.navBarLogout{
  background-color: #77af74;
  color: white;
  text-decoration: none;
  border: none;
  display: flex;
}

.navBarLink{
    color: white;
    text-decoration: none;
    font-weight: 400;
    
}


/* 
    Landing Page 
*/

.landingCards{
    height: 600px;
    width: 400px;
}

.landingCardLeft{
    background-image:url('https://i.pinimg.com/originals/5d/18/80/5d1880ee3e02d862bbcff095a8754211.jpg');
    background-size:cover;
    border: none;
}

.landingCardRight{
    display:flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 1em;
    padding-right: 1em;
    align-items: center;
}

.landingTitle{
    font-family: 'Roboto', sans-serif;
    font-weight: 800;
    font-size: 3.8em;
}

.landingButtons{
    background-color:#77af74;
    color: white; 
    text-decoration: none;
    width: 300px;
    border-radius: 0.5em;
    margin-top: 1em;
    border: none;
    padding: 0.5em 0 0.5em 0;
}



/* 
    Cards and Logging In
*/

.cardTitle{
    font-family: 'Roboto', sans-serif;
    font-weight: 800;
    font-size: 4em;
}

.loginCard{
    padding: 3em 3em 3em 3em;
    width: 600px;
    min-height: 400px;
    box-shadow: 7px 2px 25px 12px rgba(0,0,0,0.21);   
    font-family: 'Roboto', sans-serif;
    font-weight: 400; 

}

.loginLandingPage{
    display: flex;
    height: 100vh;
  
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(to top, #c1dfc4 0%, #deecdd 100%);    
}

.loginField{
    border: none;
    border-bottom: 1px solid #ecf0f1;
    width: 500px;
    margin-bottom: 1em;
    padding-top: 1em;
    padding-bottom: 1em;

}

.loginFieldError{
    color: red;
    font-weight: 700;
    
}

.loginFormButtons{
    background-color:#77af74;
    color: white; 
    text-decoration: none;
    border-radius: 0.5em;
    padding:1em 5em 1em 5em ;
    margin-right: 0.5em;
    margin-top: 1em;
    border: none;
}

.loginFormButtonContainer{
    display: flex;
    justify-content: center;
    padding-top: 3em;
}

.landingFormButtonContainer{
    display: flex;
    padding:0 3em 0 3em;
    flex-direction: column;
    justify-content: center;
    padding-top: 3em;
}


/* 
    Plant Cards  
*/

.getPlantsGroup{
    font-family: 'Roboto', sans-serif;
    margin-top: 10em;
    margin-bottom: 10em;
    display: flex;
    justify-content: center;
}

.plantCardTitle{
    font-size: 1.5em;
}

.plantCardHeader{
    height: 250px;
    position: relative;
    line-height: 3.5em;
    max-width: 350px;
    color: white;
    text-align: center;
    border-top-right-radius: 0.75em !important;
    border-top-left-radius: 0.75em !important;
    background:linear-gradient(180deg, #86b3e5 0%,#4384bf 100%);    
}    
.plantCardHeaderTop{
    font-size: 3em;
    font-weight: 700;
}

.plantCardHeaderBottom{
    font-size: 3em;
    font-weight: 700;
    position: absolute;
    top: 150px;
    width: 100%;
    left: 0;
}

.cardWaterButton{
    border-style: solid;
    border-width: 6px;
    border-color: white;
    text-decoration: none;
    height: 75px;
    width: 75px;
    border-radius:10em;
    background-color: #4384bf;
    color: white;
    position: absolute;
    left:50%;
    margin-left: -37.5px;
    top: 215px;
}

.plantCardHeaderDays{
    position: absolute;
    width: 100%;
    top: 115px;
    left: 0;
}

.plantCardBody{
    display: flex;
    flex-direction: row;
    height: 250px;
    width: 100%;
    font-size: 1.3em;
    padding: 2em 2em 2em 2em;
    
}

.cardIcons{
    display: flex;
    flex-direction: column;
    font-size: 1.5em;
    padding-right: 1em;
}

.cardDetails{
    display: flex;
    flex-direction: column;
    padding-top: 0.5em;
}  

.getPlantsCards{
    min-height: 400px;
    max-width: 350px;
    margin-bottom: 1em;
    border-radius:0.75em;
}

.plantCardButtonContainer{
    max-width: 100vw;
    display: flex;
    justify-content: space-around;
    background: gray;  
    border-bottom-right-radius: 0.75em;
    border-bottom-left-radius: 0.75em;
}
.plantCardButton{
    background: gray;
    border: none;
    color: white;
    padding-top: 0.5em;
    padding-bottom: 0.5em;

}

.getPlantButton{
    height: 250px;
    width: 250px;
    border-radius: 200px;
    margin-top: 10vw;
    font-size: 8em;
    font-weight: 100;
}

.plantFormButtons{
    display: flex;
    justify-content: center;
}

.plantFormLink{
    color: white;
    text-align: center;
}


/*
    Dashboard
*/

.addPlantButton{
    background-color: white;
    width: 200px;
    height: 200px;
    border-radius: 10em;
    border: none;
    z-index: 5;
}

.addPlantText{
    font-size: 8em;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: #77af74;
    z-index: 5;
}

.dashboardBackground{
    color: black;
    background-color: #f2f1ef;
    padding-bottom: 5em;
}

.helloDash{
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 10em;
    line-height: 1em;
    font-weight: 900;
    text-align: left;
    padding-right: 0.5em;
    z-index: 5;
}

.dashboardGroup{
    margin-top: -60em;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 8em;
    flex-wrap: wrap;
}


/*
    Edit Form
*/

.userEditFormButtonContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    color: white; 
}

.editUserFormPrimary{
    border: none;
    text-decoration: none;
    color: white;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    background-color:#77af74;
    border-radius: 0.5em;
    margin-right: 0.5em;
    width: 200px;
}

.editUserFormSecondary{
    border: none;
    text-decoration: none;
    background-color:#77af74;
    width: 200px;
    border-radius: 0.5em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

.deleteUserButton{
    background-color:red;
    color: white; 
    border: none;
    text-decoration: none;
    width: 200px;
    border-radius: 0.5em;
    margin-top: 0.5em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

/*
    Media Query 
*/

@media only screen and (min-device-width: 375px) and (max-device-height: 812px){
     .helloDash{
         font-size: 5em;
         padding-bottom: 0.5em;
         
     }

     .dashboardGroup{
        flex-direction: column;
        justify-content: center;
     }

     .addPlantButton{
        width: 100px;
        height: 100px;
        border-radius: 10em;

    }
    
    .addPlantText{
        font-size: 4em;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        color: #77af74;
        z-index: 5;
    }

    .loginCard{
        padding: 3em 3em 3em 3em;
        width: 100%;
        height: 100vh;          
    }

    .loginField{
        width: 60vw;
        margin-bottom: 1em;
        padding-top: 1em;
        padding-bottom: 1em;
    }

    .loginFormButtons{
        border-radius: 0.5em;
        padding:0.5em 3em 0.5em 3em ;
        margin-top: 1em;
    

    }    

    .loginFormButtonContainer{
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-top: 3em;
        
    }

    .landingCardLeft{
        display: none;
        
    }

    .landingCards{
        width: 100vw;
        height: 100vh;
        text-align: center;
    }

    .plantFormButtons{
        display: flex;
        justify-content: center;
    }

    .navBar{
        clip-path: ellipse(85% 50% at 40% 2%);
    }

    .editUserFormPrimary{
        margin-right: 0;
        margin-bottom: 0.5em;
    }

    .plantFormButtons{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

}